import React from 'react';
import ipad from '../assets/images/ui-project.png';

export default function About() {
  return (
    <section id="about" className="about-section text-center">
      <div className="container">
        <div className="row">
          <div className="col-lg-8 mx-auto">
            <h2 className="text-white mb-4">Bhavisha Patel</h2>
            <p className="text-white-50">
              I'm a result-oriented UX designer located in Toronto, Canada, with
              a passion for crafting delightful user experiences and efficient
              product solutions. I'm currently in pursuit of a remote or online
              UI/UX Designer position.
            </p>
          </div>
        </div>
        <img src={ipad} className="img-fluid" alt="" />
      </div>
    </section>
  );
}
