import React from 'react';

import { GatsbyImage, getImage } from 'gatsby-plugin-image';
import { Link } from 'gatsby';

export default function ProjectGroupItem({ groupIndex, item }) {
  const orderLgFirstClass =
    groupIndex % 2 === 0 ? 'col-lg-6 order-lg-first' : 'col-lg-6';
  const textLgAlignClass =
    groupIndex % 2 === 0
      ? 'project-text w-100 my-auto text-center text-lg-right'
      : 'project-text w-100 my-auto text-center text-lg-left';
  const hrClass =
    groupIndex % 2 === 0
      ? 'd-none d-lg-block mb-0 mr-0'
      : 'd-none d-lg-block mb-0 ml-0';

  return (
    <div className="row justify-content-center no-gutters mb-5 mb-lg-0">
      <div className="col-lg-6">
        <Link to={'/projects/' + item.node.frontmatter.slug}>
          <GatsbyImage
            className="img-fluid"
            image={getImage(item.node.frontmatter.project_image)}
            alt={item.node.frontmatter.title}
          />
        </Link>
      </div>
      <div className={orderLgFirstClass}>
        <div className="bg-black text-center h-100 project">
          <div className="d-flex h-100">
            <div className={textLgAlignClass}>
              <h4 className="text-white">
                <Link to={'/projects/' + item.node.frontmatter.slug}>
                  {item.node.frontmatter.title}
                </Link>
              </h4>
              <p className="mb-0 text-white-50">
                {item.node.frontmatter.project_desc}
              </p>
              <hr className={hrClass} />
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
