import React from 'react';

import { GatsbyImage, getImage } from 'gatsby-plugin-image';
import { Link } from 'gatsby';
import ProjectGroupItem from './ProjectGroupItem';
import { useStaticQuery, graphql } from 'gatsby';

export default function ProjectGroup(props) {
  const data = useStaticQuery(graphql`
    query {
      allMdx {
        edges {
          node {
            id
            excerpt
            frontmatter {
              title
              index
              group
              slug
              project_desc
              project_image {
                id
                absolutePath
                childImageSharp {
                  gatsbyImageData(
                    placeholder: BLURRED
                    formats: [AUTO, WEBP, AVIF]
                  )
                }
              }
            }
          }
        }
      }
    }
  `);
  const itemIndex = (props.groupIndex - 1) * 2 + 1;
  const oddNode = data.allMdx.edges
    .filter((edge) => edge.node.frontmatter.group === props.groupName)
    .find((edge) => {
      return edge.node.frontmatter.index === (props.groupIndex - 1) * 2 + 1;
    });
  const evenNode = data.allMdx.edges
    .filter((edge) => edge.node.frontmatter.group === props.groupName)
    .find((edge) => {
      return edge.node.frontmatter.index === (props.groupIndex - 1) * 2 + 2;
    });
  return (
    <section className="projects-section bg-light">
      <div className="container">
        <ProjectGroupItem
          groupIndex={itemIndex}
          groupName={props.groupName}
          item={oddNode}
        />
        <ProjectGroupItem
          groupIndex={itemIndex + 1}
          groupName={props.groupName}
          item={evenNode}
        />
      </div>
    </section>
  );
}
