import React from 'react';
import ProjectGroup from './ProjectGroup';

export default function Projects() {
  return (
    <>
      <section id="projects">
        <ProjectGroup groupName="ui-projects" groupIndex="1" />
        <ProjectGroup groupName="ui-projects" groupIndex="2" />
        <ProjectGroup groupName="ui-projects" groupIndex="3" />
      </section>
      <section id="visuals">
        <ProjectGroup groupName="graphic-projects" groupIndex="1" />
        <ProjectGroup groupName="graphic-projects" groupIndex="2" />
        <ProjectGroup groupName="graphic-projects" groupIndex="3" />
        <ProjectGroup groupName="graphic-projects" groupIndex="4" />
      </section>
    </>
  );
}
