import React, { Component } from 'react';
import Scroll from './Scroll';
import { Link } from 'gatsby';

import { StaticImage } from 'gatsby-plugin-image';
import PropTypes from 'prop-types';
//import logo from '../assets/images/bhavisha.png';
export default class Header extends Component {
  static propTypes = {
    mode: PropTypes.string,
  };
  constructor(props) {
    super(props);
    this.state = {
      openMenu: false,
      mode: this.props.mode,
      visibilityClass: this.props.mode === 'sub' ? 'navbar-shrink' : '',
    };
  }
  toggleMenu = (value) => {
    this.setState({ openMenu: value });
  };
  handleScroll = () => {
    const { visibilityClass, mode } = this.state;
    if (window.pageYOffset > 300 || mode === 'sub') {
      if (visibilityClass !== 'navbar-shrink') {
        this.setState({ visibilityClass: 'navbar-shrink' });
      }
    } else {
      if (visibilityClass === 'navbar-shrink') {
        this.setState({ visibilityClass: '' });
      }
    }
  };

  componentDidMount() {
    window.addEventListener('scroll', this.handleScroll);
  }
  componentWillUnmount() {
    window.removeEventListener('scroll', this.handleScroll);
  }

  render() {
    const { openMenu, visibilityClass } = this.state;

    return (
      <nav
        className={`navbar navbar-expand-lg navbar-light fixed-top ${visibilityClass}`}
        id="mainNav"
      >
        <div className="container">
          <Link className="navbar-brand navbar-logo" to="/">
            {/*<img className="img-fluid" src={logo} alt="" />*/}
            <StaticImage
              src="../images/bhavisha.png"
              alt="Bhavik Patel"
              className="img-fluid"
            />
          </Link>

          <button
            onClick={(_) => this.toggleMenu(!openMenu)}
            className={`navbar-toggler navbar-toggler-right ${
              openMenu ? '' : 'collapsed'
            }`}
            type="button"
            aria-controls="navbarResponsive"
            aria-expanded={openMenu}
            aria-label="Toggle navigation"
          >
            Menu
            <i className="fas fa-bars"></i>
          </button>
          <div
            className={`collapse navbar-collapse ${openMenu ? 'show' : ''}`}
            id="navbarResponsive"
          >
            <ul className="navbar-nav ml-auto">
              <li className="nav-item">
                <Scroll
                  onClick={(_) => this.toggleMenu(!openMenu)}
                  type="id"
                  element="projects"
                >
                  <Link className="nav-link" to="#projects">
                    PROJECTS
                  </Link>
                </Scroll>
              </li>
              <li className="nav-item">
                <Scroll
                  onClick={(_) => this.toggleMenu(!openMenu)}
                  type="id"
                  element="visuals"
                >
                  <Link className="nav-link" to="#visuals">
                    VISUALS
                  </Link>
                </Scroll>
              </li>

              <li className="nav-item">
                <Scroll
                  onClick={(_) => this.toggleMenu(!openMenu)}
                  type="id"
                  element="about"
                >
                  <Link className="nav-link" to="/#about">
                    ABOUT
                  </Link>
                </Scroll>
              </li>
              <li className="nav-item">
                <a
                  className="nav-link"
                  href="https://www.bhavisha.ca/bhavisha-resume.pdf"
                  target="_blank"
                  rel="noreferrer"
                >
                  RESUME
                </a>
              </li>
            </ul>
          </div>
        </div>
      </nav>
    );
  }
}
